
.center{
   display: flex;
   justify-content: center;
   align-items: center;
}
input[type="checkbox"]{
position: relative;

width: 40px;
height: 20px;
appearance: none;
background-color:$searchBarBackground;
outline: none;
border-radius: 20px;
box-shadow: inset 0 0 5px rgba(0,0,0,.2);
transition: .5s;
}


input:checked[type="checkbox"]{

background-color: $navColor;

}

input[type="checkbox"]::before{
content: '';
position: absolute;
width: 20px;
height: 20px;
border-radius: 20px;
top: 0;
left: 0;
background-color: #fff;
box-shadow: 0 2px 5px rgba(0,0,0,.2);
transform: scale(1.1);
transition: .5s;

}

input:checked[type="checkbox"]::before{

left: 20px;



}